.container {
    background-color: rgb(22, 15, 20);
    padding: 0.2rem;   
}

.container.list {
    width: 100%;
}

.container.grid {
    width: 50%;
}


.video {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    border: 1px solid black;
    box-shadow: 3px 3px 5px 0px rgba(191, 191, 191, 0.53);
    cursor: pointer;
    transition: transform 250ms ease-in;
}

.video:hover {
    transform: scale(1.02);
}

.thumbnail {
    width: 40%;
    height: 100%;
}

.metadata{
    margin-left: 0.2em;
    color: white;
}

.title
,.channelTitle {
    font-size: 0.8rem;
    margin: 0;
}

.channelTitle {
    font-size: 0.6rem
}

@media screen and (max-width: 48rem) {
    .container.grid {
        width: 100%;
    }
}